main p,
main div {
  margin: 0;
  padding: 0;
}
.add-post-box {
  display: block;
  width: 55%;
  margin: auto;
  margin-bottom: 50px;
}
.add-post-form {
  padding: 30px 50px;
  /* height: 902px; */
  background: #cdeef9;
  background: #d4f0f8;
  border-radius: 9px;
  box-shadow: 2px 2px 5px 3px rgba(23, 170, 214, 0.75);
  position: relative;
}
.add-post-form p {
  margin-top: 10px;
}
.add-post-form .name {
  width: 40%;
}
.add-post-form textarea {
  width: 100%;
}
.category-box {
  display: flex;
  width: 90%;
  margin: auto;
  flex-flow: wrap;
  row-gap: 10px;
  column-gap: 20px;
  margin-top: 15px;
}
.category-box .loading-cat {
  background-color: unset !important;
}
.check {
  height: 55px;
  width: 55px;
  margin-bottom: 5px;
}
.map {
  width: 100%;
  height: 234px;
  border: 2px solid #000000;
  box-sizing: border-box;
  border-radius: 5px;
}
.map-inp {
  width: 80%;
}
.help {
  width: 80%;
  list-style: none;
  background-color: rgb(221, 240, 241);
  box-sizing: border-box;
  border-radius: 4px;
  padding: 0;
}
.help li {
  cursor: pointer;
  padding: 3px 0 0 20px;
}
.help li:hover {
  background-color: rgb(221, 223, 223);
}
.photos {
  display: grid;
  grid-template-columns: auto auto auto;
  column-gap: 4%;
  row-gap: 10px;
}
.photos img {
  max-width: 30%;
  max-height: 200px;
}
.upload {
  width: 150px;
  margin-left: calc(100% - 150px);
}
.photo-upload {
  width: 40%;
}
@media screen and (max-width: 1900px) {
  .add-post-box {
    width: 65%;
  }
}
@media screen and (max-width: 1450px) {
  .add-post-box {
    width: 80%;
  }
}
@media screen and (max-width: 1000px) {
  .add-post-box {
    width: 90%;
  }
}
@media screen and (max-width: 1000px) {
  .add-post-form .name {
    width: 60%;
  }
  .add-post-box {
    width: 90%;
  }
  .add-post-form {
    padding: 20px 25px;
  }
}
@media screen and (max-width: 800px) {
  .category-box {
    grid-template-columns: auto auto;
  }
  .fl-between {
    flex-direction: column;
  }
}
@media screen and (max-width: 600px) {
  .add-post-form {
    padding: 10px 15px;
  }
  .add-post-form .name {
    width: 80%;
  }
}
